.creators {
    position: relative;
    padding-bottom: 100px;

    @include onTablet {
      padding-bottom: 140px;
    }

    @include onDesktop {
      padding-bottom: 200px;
    }

    &__wrapp {
      @include pageGrid;
    }

    &__section-title {
      grid-column: 1/-1;

      @include onTablet {
        grid-column: 1/4;
      }

      @include onDesktop {
        grid-column: 2/6;
      }
    }


    &__paragraf {
      font-family: $main-text-font-family;
      color: $main-text-color;
      font-size: 14px;
      font-weight: 400;
      line-height: 150%;
      margin-bottom: 30px;
      grid-column: 1/-1;
      margin-top: 10px;

      @include onTablet {
        font-size: 16px;
        grid-column: 1/4;
        margin-top: 10px;
      }

      @include onDesktop {
        font-size: 16px;
        margin-top: 32px;
        grid-column: 2/6;
      }


      &--desktop {
        font-family: $main-text-font-family;
        color: $main-text-color;
        font-size: 16px;
        font-weight: 400;
        line-height: 150%;
        display: none;

        @include onDesktop {
          display: block;
          grid-column: 2/6;
        }
      }
    }

    &__images {
      position: absolute;
      right: 130px;

      @include onDesktop {
        grid-column: 7/13;
      }
    }

    &__img--1 {
      display: none;

      @include onTablet {
        display: none;
      }

      @include onDesktop {
        grid-column: 7/10;
        display: block;
      }
    }

    &__link {
      color: $second-text-color;
      font-family: $main-text-font-family;
      font-size: 14px;
      font-weight: 500;
      line-height: 130%;
      text-decoration: none;
      text-transform: uppercase;
      padding-bottom: 10px;
      border-bottom: 1px solid rgba(52, 79, 16, 0.5);
      cursor: pointer;
      display: block;
      max-width: max-content;
      transition: all .3s;
      margin-top: 40px;
      grid-column: 1/-1;

      @include onTablet {
        grid-column: 1/-1;
      }

      @include onDesktop {
        grid-column: 2/4;
      }

      &:hover {
        border-bottom: 1.5px solid $dark-green;
      }
    }

    &__button {
      display: block;
      color: $color;
      border: none;
      font-family: $main-text-font-family;
      font-size: 16px;
      width: 280px;
      height: 60px;
      background: $main-text-color;
      text-transform: uppercase;
      margin: 0;
      margin-top: 25px;
      text-align: center;
      justify-content: center;
      grid-column: 1/-1;

      @include onTablet {
        grid-column: 1/3;
        margin-top: 30px;
      }

      @include onDesktop {
        grid-column: 2/4;
        margin-top: 50px;
      }

      &:hover {
        scale: 105%;
        background-color: $beige-color;
        color: $main-text-color;
      }
      
        &:active {
          background-color: $background-color-button;
          scale: 98%;
        }
    }
}